@import url(../variables.css);

.asideContainer {
  width: var(--aside-container-width);
  padding: 16px;
  overflow-y: scroll;
}
.asideContainer::-webkit-scrollbar {
  display: none;
}
.asideInput {
  width: 100%;
  height: 50px;
  border-radius: 50px;
  padding: 8px 8px 8px 50px;
  border: 1px solid var(--line-gray-color);
  font-size: var(--input-font-size);
}
.asideBox {
  margin-top: 16px;
  margin-bottom: 12px;
  width: 100%;
  height: 400px; /* 임시 height */
  border-radius: 16px;
  border: 1px solid var(--line-gray-color);
}
@media screen and (max-width: 1085px) {
  .asideContainer {
    width: 330px;
  }
}
@media screen and (max-width: 1020px) {
  .asideContainer {
    display: none;
  }
}
